import React from 'react';
import Footer from './Footer';
import { Chip } from '@mui/material';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import ProgressBar from 'react-bootstrap/ProgressBar';
import db from 'libs/db';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import CurrencyInput from 'react-currency-input-field';

export default function FinancialDependents(props) {

	const [dependents, setDependents] = React.useState("");

	const [loading, setLoading] = React.useState(true);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const STATE = props.location.state

	const chipset = [
		{ "key": "0", "name": "0" },
		{ "key": "1", "name": "1" },
		{ "key": "2", "name": "2" },
		{ "key": "3", "name": "3" },
		{ "key": "4", "name": "4" }
	]

	React.useEffect(async ()=>{
		await db.get(db.model.riskProfile).then(p=>{
			if(p){
				setDependents(p.dependents || "")
			} else {
				setDependents("")
			}
		})
		setLoading(false)
	},[])

	const checkValidation = () => {
		if (dependents) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [dependents])

	if (STATE === undefined) {
		props.history.push('/Dashboard')
		return <></>
	}
	return (
		<div className='mainContainer'>
			<MainHeader
				header="Identifying your risk profile"
				navigation={props.history}
			/>
			<ProgressBar now={18} className="progress-bar-custom" />
			<div className="dataContainer" style={{paddingTop:'42px'}}>
				{
					loading ?
					<ActivityLoaderMintbox />
						:
						<>
							<div className="bodyTextDark">How many financial dependents do you have?</div>
							<CurrencyInput
								className='normalInput'
								id="dependents"
								name="dependents"
								placeholder="2"
								value={dependents}
								allowDecimals={false}
								allowNegativeValue={false}
								maxLength={2}
								disableAbbreviations={true}
								autoFocus
								suffix=''
								onValueChange={(value) => setDependents(value)}
								/>
							<div className='hiddden-scrollbar'>
								{chipset.map((item) => {
									return (
										<Chip
											className={dependents===item.key?"normalChip selected":"normalChip"}
											key={item.key}
											label={item.name}
											onClick={() => {
												setDependents(item.name)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/risk-profile/3"
				state={{...STATE,dependents}}
				saveData = {{ dependents }} 
				buttonText="Confirm"
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
